.settings{
  display: flex;
  flex-direction: row;
  width: 50%;
  justify-content: space-around
}



.piano {
  display: grid;
  column-gap: 5px;
}


.white {
  background-color: white;
  grid-row: 2; /* Place in first row */

}

.black {
  background-color: black;
  grid-row: 1; /* Place in first row */
}

.highlight {
  background-color: #D4A5A5;
  border-radius: 50%;
  border: 5px solid #9E9E9E;
  transform: scale(1.2);

}


.note-name {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  color: black;
}

.black .note-name {
  color: white;
}



/* Practice Mode */
.scale-checkbox-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
